import { Navigate } from "react-router-dom";
import { connect } from "react-redux";

import { rbTestEncrypt } from "../../utils/crypto";
import { useGetWalletQuery } from "../../service/api/meveoApi";
import { updateWallet } from "../../store/slices/walletSlice";

import Loader from "../Loader";
import { useEffect } from "react";
import { updateMetamask } from "../../store/slices/authSlice";
import { refreshToken } from "../../store/actions/authActions";

const RequireUser = props => {
  const { accessToken, children, storeWallet, updateMetamask, refreshToken } = props;

  const { data, isFetching } = useGetWalletQuery(null, {
    skip: !accessToken
  });

  useEffect(() => {
    if ( !isFetching ) {
      // Update wallet's information
      if ( !!data && data.status === 'success' ) {
        const originData = data?.result; 
        storeWallet({
          public: originData.publicInfo,
          private: {
            phone: {
              number: originData.privateInfo?.phoneNumber.phoneNumber,
              dialCode: originData.privateInfo?.dialCode.dialCode,
              verified: originData.privateInfo?.phoneNumber.verified,
            },
            email: {
              address: originData.privateInfo?.email.emailAddress,
              verified: originData.privateInfo?.email.verified,
            },
            username: originData.privateInfo?.username
          },
          address: originData.address
        });
        updateMetamask(originData.privateInfo?.seedPhrase);
      }

    }
  }, [isFetching])

  if ( !accessToken ) {
    return <Loader navigate="/token-login" />;
  } 

  return children;
};

export default connect(state => {
  let auth = state.auth
  return {
    accessToken: auth.accessToken,
    expiredAt: auth.expiredAt,
    refreshToken: auth.refreshToken
  }
}, dispatch => {
  return {
    storeWallet: (data) => {
      dispatch(updateWallet(data));
    },
    updateMetamask: data => {
      dispatch(updateMetamask(data));
    },
    refreshToken: () => {
      dispatch(refreshToken())
    }
  }
})(RequireUser);

