import { createSlice } from "@reduxjs/toolkit";
import { getWallet } from "../actions/walletActions";

const initialState = {
  status: 'idle',
  error: null,
  public: null,
  private: {
    phone: {
      number: null,
      dialCode: 0,
      verified: false,
    },
    email: {
      address: null,
      verified: false,
    },
    username: null
  },
  address: '',
}

const walletSlice = createSlice({
  name: "profile",
  initialState: initialState,
  reducers: {
    updateWallet: (state, action) => {
      const data = action.payload;
      
      state.public = data.public;
      state.private = data.private;
      state.address = data.address;
      state.status = 'success';
      state.error = null;
    }
  },
  extraReducers: builder => {
    builder
      .addCase(getWallet.pending, (state, action) => {
        state.status = 'loading'
      })
      .addCase(getWallet.fulfilled, (state, action) => {
        const data = action.payload;
        state.status = 'success';
        if ( data.wallet ) {
          state.public = data.wallet.public;
          state.private = data.wallet.private;
          state.address = data.wallet.address;
        }
        state.error = null;
      })
      .addCase(getWallet.rejected, (state, action) => {
        state.status = 'failed';
        state.error = action.payload;
        state.public = initialState.public;
        state.private = initialState.private;
        state.address = initialState.address;
      })
  }
});

export const { updateWallet } = walletSlice.actions;
export default walletSlice.reducer;