import { combineReducers, configureStore } from '@reduxjs/toolkit';
import { persistReducer, persistStore } from 'redux-persist';
import storage from 'redux-persist/lib/storage';
import thunk from 'redux-thunk';
import { meoveoRestApi } from '../service/api/meveoApi';
import { rtkQueryErrorLogger } from './midleware/apiMidleware';
import { cryptoMidleware } from './midleware/cryptoMidleware';

import authReducer, { resetError } from './slices/authSlice';
import walletReducer from './slices/walletSlice';
import cryptoReducer from './slices/cryptoSlice';

const persistConfig = {
  key: 'unikbase-web-app',
  storage,
  whitelist: [
    'auth',
  ],
};

const rootReducer = combineReducers({
  auth: authReducer,
  wallet: walletReducer,
  crypto: cryptoReducer,
  [meoveoRestApi.reducerPath]: meoveoRestApi.reducer,
});

const persistedReducer = persistReducer(persistConfig, rootReducer);

const store = configureStore({
  reducer: persistedReducer,
  devTools: process.env.NODE_ENV !== 'production',
  middleware: [thunk, rtkQueryErrorLogger, meoveoRestApi.middleware],
});

export const persistor = persistStore(store, null, () => {
  store.dispatch(resetError());
});

export default store;